import { getAbsoluteUrl, _extends } from './util'

// 区分设备系统
var ua = navigator.userAgent;
var android = ua.match(/(Android);?[\s\/]+([\d.]+)?/);
var ipad = ua.match(/(iPad).*OS\s([\d_]+)/);
var ipod = ua.match(/(iPod)(.*OS\s([\d_]+))?/);
var iphone = !ipad && ua.match(/(iPhone\sOS)\s([\d_]+)/);
var isFromApp = !!ua.toLowerCase().match(/DFYJ/i);

// 初始化桥接对象
var init = function (bridge) {
    bridge.init(function(message, responseCallback) {
        if (responseCallback)
            responseCallback(data)
    })

    return bridge
}

/**
 * 初始化桥接对象
 * connectWebViewJavascriptBridge
 * @param callback
 */
var ready = function (callback) {
    if (isFromApp){
        if (ipad || iphone || ipod) {
            // 新版本兼容IOS
            if (window.WebViewJavascriptBridge) { return callback(WebViewJavascriptBridge); }
            if (window.WVJBCallbacks) { return window.WVJBCallbacks.push(callback); }
            window.WVJBCallbacks = [callback];
            var WVJBIframe = document.createElement('iframe');
            WVJBIframe.style.display = 'none';
            WVJBIframe.src = 'https://__bridge_loaded__';
            document.documentElement.appendChild(WVJBIframe);
            setTimeout(function() { document.documentElement.removeChild(WVJBIframe) }, 0)
        }
        else if (android) {
            // 旧版本兼容Android
            if (window.WebViewJavascriptBridge) {
                callback(init(WebViewJavascriptBridge))
            } else {
                document.addEventListener('WebViewJavascriptBridgeReady', function() {
                    // 以异步形式加载
                    callback(init(WebViewJavascriptBridge))
                }, false)
            }
        }
        else {
            callback()
        }
    }
    else{
        callback()
    }
}

// 空函数
var noop = function () {}

/**
 * APP WebView后退，后退到栈的最外层
 * @param options
 */
var onBack = function (options) {
    // 默认配置
    var _options = {
        complete: noop
    };
    // 复制
    _options = _extends({}, _options, options);

    WebViewJavascriptBridge.callHandler('back', _options, function(response) {
        _options.complete(response);
    });
}

/**
 * APP WebView刷新
 * @param options
 */
var onRefresh = function (options) {
    var _options = {
        complete: noop
    };
    _options = _extends({}, _options, options);

    WebViewJavascriptBridge.callHandler('refresh', _options, function(response) {
        _options.complete(response);
    });
}

/**
 * 设置原生导航条
 * @param options
 * {
 *    "title": "xxxxx",
 *    "type": "0"       // 0-默认 1-切换类
 * }
 */
var onNavigationBar = function (options) {
    var _options = {
        complete: noop
    };
    _options = _extends({}, _options, options);

    WebViewJavascriptBridge.callHandler('navigationBar', _options);
}

/**
 * 打开一个新的webview
 * @param options
 * {
 *    "title": "xxxxx",      // 导航名称，如果html设置title，以html为主
 *    "uri": "xxxxxxx"       // webview请求的路由地址，目前只支持http协议，今后可支持shema协议
 * }
 */
var onPushView = function (options) {
    var _options = {
        title: ''
    };
    _options = _extends({}, _options, options);

    if (!!_options.uri){
        if (_options.uri.indexOf('http') != -1 || _options.uri.indexOf('https') != -1) {

        } else {
            _options.uri = getAbsoluteUrl(_options.uri);
        }
    }

    WebViewJavascriptBridge.callHandler('pushView', _options);
}

/**
 * 原生页面将要出现时触发的事件
 * @param callback
 */
var pageWillAppear = function (callback) {
    if (typeof callback == 'function')
        WebViewJavascriptBridge.registerHandler("pageWillAppear", callback)
}

/**
 * H5发送通知给客户端
 * @param options
 */
var onNotify = function (options) {
    var _options = {
        type: 'javascriptNotify'
    };
    _options = _extends({}, _options, options);

    WebViewJavascriptBridge.callHandler('notify', _options);
}

/**
 * H5注册通知，由客户端进行通知
 * @param type  事件类型名称
 * @param callback  回调处理
 */
var subscribeNotify = function (type, callback) {
    if (typeof callback == 'function') {
        WebViewJavascriptBridge.registerHandler(type, callback)
    }
}

/**
 * 调用Schema Url
 * @param options
 */
var onDynamicLink = function (options) {
    var _options = {
        url: ''
    };
    _options = _extends({}, _options, options);

    WebViewJavascriptBridge.callHandler('dynamicLink', _options);
}

/**
 * 调用Request指令（通常作用在一些原生和客户端之间比较特殊的操作）
 * @param operationType     操作指令（见文档）
 * @param payloadOptions    消息体结构
 */
var onRequest = function (operationType, payloadOptions, okCallBack, notOkCallBack) {
    var _options = {
        type: !operationType ? '1' : operationType,
        payload: !payloadOptions ? {} : payloadOptions,
    }

    WebViewJavascriptBridge.callHandler('request', _options, function (response) {
        if (typeof response == 'string'){
            response = JSON.parse(response);
        }

        if (response.error || response.error === 'true'){
            if (typeof notOkCallBack === 'function'){
                notOkCallBack(response.info)
            }
        }
        else{
            if (typeof okCallBack === 'function'){
                okCallBack(response.payload)
            }
        }
    })

}

export {
    // ready是一个异步操作,如果需要在页面加载时就调用相关接口,则需把相关接口放在ready函数中调用来确保正确执行。对于用户触发时才调用的接口,则可直接调用,不需要放在ready函数中
    // 该函数中有一个bridge变量,与下面的bridge使用上一致,只是调用顺序需要额外注意
    ready,
    onBack,
    onRefresh,
    onNavigationBar,
    onPushView,
    onNotify,               // H5发送通知给客户端
    subscribeNotify,        // 客户端发送通知给H5
    onDynamicLink,
    onRequest,

    // 原生生命周期相关事件
    pageWillAppear
}





